  position: relative;
  .btn-exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
  .iconfont {
    margin-right: 4px;
    &.icon-xuanzewenjian {
      font-size: 12px;
    }
    &.icon-xiazai {
      font-size: 13px;
    }
    &.icon-upload {
      font-size: 12px;
    }
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
}
